.mainContent {
  margin-left: 10%;
  margin-right: 10%;
  width: 75%;
}

body {
  background-color: #042645;
  color: #DCDCDC;
  margin: 0;
}

header{
  margin-top: 20px;
  display:flex;
  flex-direction: column;
}

nav{
  display: flex;
  flex-wrap: nowrap;
  margin:auto;
}

main {
  font-family: 'Ubuntu', sans-serif;
  margin-left : 30px;
  margin-right: 30px;
}




.mainTitle{
  display: flex;
  margin: auto;
  margin-bottom: 30px;
}


/* Portfolio styling */

.basicInfo {
  display: flex;
  width: 70%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.skills-container {
  display: flex;
  justify-content: space-between;
}

.skills-list {
  width: 30%;
}

.projectList {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.project {
  width: 40%;
  margin-bottom: 30px;
  height: auto;
}

.project img {
  width: auto;
  max-width: 100%;
  object-fit: cover;
}


a {
  /* white colored link with underline even after pressed */
  color: #DCDCDC
}

@media only screen and (max-width: 800px) {
  nav{
      justify-content: center;
  }
  main {
    justify-content: center;
  }
  footer {
      width:96.5%;
      border-radius: 0;
  }
}
